export default class Helper{
    static getHightBigger(){
        if(window.innerWidth < 640){
            return "500px"
        }else{
            return "500px"
        }
    }

    static getWidthBigger(){
        if(window.innerWidth < 640){
            return "300px"
        }else{
            return "500px"
        }
    }

    static getHightSmaller(){
        if(window.innerWidth < 640){
            return "1rem"
        }else{
            return "4rem"
        } 
    }

    static getWidthSmaller(){
        if(window.innerWidth < 640){
            return "1rem"
        }else{
            return "4rem"
        }  
    }
}