import React, { useLayoutEffect, useRef, useState } from "react";
import "./PageComponent.css";

import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { CSSPlugin } from "gsap/CSSPlugin";
gsap.registerPlugin(ScrollTrigger, CSSPlugin);
function PageComponent({ page }) {
  /**
   * {
   *  title,
   *  first_description,
   *  second_description,
   *  contents:[
   *      {
   *          title,
   *          descriptions:[]
   * }
   * ]
   * }
   */

  const { title, first_description, second_description, contents } = page;
  useLayoutEffect(() => {
    const tl = gsap
      .timeline({
        //definition of scroll trigger
        scrollTrigger: {
          trigger: "descriptions_content",
          scrub: true,
          start: "top center",
        },
        defaults: { duration: 0.5, ease: "none" },
      })
      .from(".titles_content", { opacity: 0.8, scale: 0.8 })
      .from("descriptions_content", { opacity: 0.8, scale: 0.8 }, "<");
    return () => {
      tl.scrollTrigger.kill();
      tl.kill();
    };
  }, []);
  return (
    <>
      <div
        className="w-screen h-screen pt-12"
        style={{ backgroundColor: "#041016" }}
      >
        <div className="titles w-full h-full ">
          <div
            className="main_title_component font-normal h-full m-auto flex flex-wrap content-end "
            style={{ color: "#C1EEFF" }}
          >
            <h1 className="first_title w-full">{title}</h1>
            <br />
            <h2 className=" first_description w-full">
              {typeof first_description() !== "boolean"
                ? first_description()
                : ""}
            </h2>
            <h4 className="second_description">
              {typeof second_description() !== "boolean"
                ? second_description()
                : ""}
            </h4>
          </div>
        </div>
        {contents.map((c) => {
          return (
            <div className="contentc">
              <div className="titles_content">{c.title()}</div>
              <div className="descriptions_content">
                {c.descriptions.map((descr) => {
                  return (
                    <span className="descriptions_content_box">{descr()}</span>
                  );
                })}
              </div>
            </div>
          );
        })}
      </div>
    </>
  );
}

export default PageComponent;
