import React, { useLayoutEffect, useRef, useState } from "react";
import "./Menu.css";
import Logo from "../../Img/logotipo.svg";
import { isMobile } from "react-device-detect";
import { useNavigate, useLocation } from "react-router-dom";
import { Button, Paper } from "@material-ui/core";
import Menu from "@material-ui/icons/Menu";

function Header(props) {
  const navigate = useNavigate();
  const location = useLocation()["pathname"];

  const [open, setOpen] = useState(false);

  console.log("location: " + JSON.stringify(location));
  const tabs = [
    { title: "HOME", link: "/" },
    { title: "CHI SIAMO", link: "/about_us" },
    { title: "COSA FACCIAMO", link: "/what_we_do" },
    { title: "ESPERIENZE E MERCATI", link: "/esperienze_mercati" },
    { title: "LAVORA CON NOI", link: "/work_with_us" },
    { title: "CONTATTI", link: "/contact_us" },
  ];
  function GetRightMenu() {
    if (isMobile)
      return (
        <div className="main_menu fixed w-screen h-12 flex flex-row">
          <img className={"ml-5"} style={{ width: "150px", height: "100%" }} src={Logo} alt="Logo cyberloop"/>
          <div className="flex flex-row justify-end w-full h-10 mt-1 mr-5">
            <Button
              id="basic-button"
              onClick={() => (open ? setOpen(false) : setOpen(true))}
              style={{
                color: open ? "#c1eeff" : "#0582ca",
                border: open ? "1px solid #c1eeff" : "1px solid #0582ca",
              }}
            >
              <Menu />
            </Button>
            {open && (
              <Paper
                style={{
                  position: "absolute",
                  top: "45px",
                  rigth: 1,
                  width: "230px",
                  height: "fit-content",
                  backgroundColor: "white",
                  textAlign: "end",
                  backgroundColor: "#041016",
                }}
              >
                {tabs.map((tab) => {
                  return (
                    <>
                      <Button
                        onClick={() => {
                          navigate(tab["link"]);
                          setOpen(false);
                        }}
                        style={{
                          color:
                            tab["link"] === location ? "#c1eeff" : "#0582ca",
                          padding: "0px 15px",
                          height: "40px",
                        }}
                        disabled={tab["link"] === location ? true : false}
                      >
                        {tab["title"]}
                      </Button>
                      <br />
                    </>
                  );
                })}
              </Paper>
            )}
          </div>
        </div>
      );
    else
      return (
        <div className="main_menu fixed w-screen h-20 flex flex-row">
          <img style={{ width: "170px", height: "100%" }} src={Logo} />
          <div className="flex flex-row justify-end justify-items-end w-full h-10 mt-5 mr-5">
            {tabs.map((tab) => {
              let dis = tab["link"] === location ? true : false;
              console.log(
                "link: " +
                  tab["link"] +
                  " location: " +
                  location +
                  " result " +
                  dis
              );
              return (
                <Button
                  onClick={() => navigate(tab["link"])}
                  style={{
                    color: tab["link"] === location ? "#c1eeff" : "#0582ca",
                    padding: "0px 15px",
                    height: "40px",
                    cursor: "None",
                  }}
                  disabled={dis}
                >
                  {tab["title"]}
                </Button>
              );
            })}
          </div>
        </div>
      );
  }
  return (
    <>
      <GetRightMenu />
      {props.children}
    </>
  );
}

export default Header;
